export default {
	data() {
		return {
			nav: [],
			dataList: [],
			useType: '',
			nodata:false,
			nomore:false,
			pageIndex: 1,
			pageSize: 20,
			pageTotal: 1,
			totals:{},
			xpTypes:[]
		};
	},
	onShow() {
		if (getApp().globalData.isShowPic) {
			getApp().globalData.isShowPic = false
		} else {
			this.setParams();
			this.getTabList();
			this.getData();
			this.accountInfo()
		}
	},
	methods: {
		getTabList() {
			let self = this
			this.$acFrame.HttpService.virtualTab().then(res => {
				if (res.success) {
					let list = res.data
					list.unshift({
						key: '',
						val: '热销'
					});
					list.filter((v, i) => {
						if (i == 0) {
							v.choose = true
						} else {
							v.choose = false
						}
					})
					self.nav = list
				}
			})
		},
		getData() { // virtualList
			let self = this
			let params = {
				pageIndex: this.pageIndex,
				pageSize: this.pageSize,
				useType:this.useType 
			}
			this.$acFrame.HttpService.virtualList(params).then(res => {
				if (res.success) {
					self.pageTotal = res.data.pageTotal
					let list = res.data.rows
					if(list.length>0){
						list.filter(v => {
							v.imgPath = self.$acFrame.Util.setImgUrl(v.imgPath);
							v.choose=false
							if(v.totalNums){
								if(v.totalNums>9999){
									v.totalNums = Math.round(v.totalNums/100)/100+'万件'
								}else{
									v.totalNums=v.totalNums+'件'
								}
							}else{
								v.totalNums='0件'
							}
						})
						self.dataList = self.dataList.concat(list)
					}else{
						self.nodata=true
					}
					
				}
			})
		},
		gotoDetail(item){
			uni.navigateTo({
				url:`spiritDetail?id=${item.id }&useType=${item.useType}`
			})
		},
		loadMore(){
			if(this.pageTotal>this.pageIndex){
				this.pageIndex++
				this.getData()
			}else{
				this.nomore=true
			}
		},
		changeNav(ind,type) {
			this.nav.filter((v, i) => {
				if (i == ind) {
					v.choose = true
				} else {
					v.choose = false
				}
			})
			this.setParams()
			this.useType = type
			this.getData()
		},
		setParams(){
			this.pageIndex=1
			this.dataList=[]
			this.nodata=false
			this.nomore=false
		},
		linkTo(name,type) {
			let url = ''
			debugger
			if(name=='activityLevel'){
				url+=name+'?type='+type
			}
			uni.navigateTo({
				url: url,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		accountInfo(){  //1.人名币2.星票3.活跃度
			let self =this
			let params = {
				type:2
			}
			this.$acFrame.HttpService.accountNums(params).then(res=>{
				if(res.success){
					self.totals = res.data
				}
			})
		},
		
	}
}
